<template>
  <div class="login">
    <div class="box">
      <p class="title">兑宝商家核销系统</p>
      <div class="input-box">
        <mt-field label="用户名" placeholder="请输入用户名" v-model="username"></mt-field>
        <mt-field label="密码" placeholder="请输入密码" type="password" v-model="password"></mt-field>
      </div>
      <div class="button-box">
        <mt-button type="primary" @click="login_func">登录</mt-button>
      </div>
    </div>
    <mt-popup v-model="popupVisible" position="top" class="mint-popup-text" :modal="false">
      {{popupText}}
    </mt-popup>
  </div>
</template>

<script>
  import {Field, Button, Indicator, Popup} from 'mint-ui';
  import qs from 'qs';

  export default {
    name: "login",
    data() {
      return {
        username: '',
        password: '',
        popupVisible: false,
        popupText: ''
      }
    },
    created() {

    },
    methods: {
      login_func() {
        let self = this;
        Indicator.open({
          text: '登录中...',
          spinnerType: 'fading-circle'
        });
        self.$http.post(self.baseUrl + '/dbhexiao/admin.php/Admin/Login/login', qs.stringify({
          username: self.username,
          password: self.password,
        })).then((result) => {
          Indicator.close();
          if (parseInt(result.data.code) === 1 && typeof result.data.data === "object") {
            let temp = result.data.data;
            self.$router.push({name: 'Index'});
          } else {
            self.popupText = result.data.message;
            self.popupVisible = true;
            setTimeout(() => {
              self.popupVisible = false;
            }, 2000);
          }
        }, (error) => {
          self.popupText = '网络连接失败！';
          self.popupVisible = true;
          setTimeout(() => {
            self.popupVisible = false;
          }, 2000);
          Indicator.close();
        })
      }
    },
    components: {
      'mt-field': Field,
      'mt-button': Button,
      'mt-popup': Popup,
    },
  }
</script>

<style scoped>
  .login {

  }

  .box {
    position: absolute;
    top: 50%;
    left: 40px;
    right: 40px;
    transform: translateY(-50%);
  }

  .box > .title {
    font-size: 24px;
    text-align: center;
    margin-bottom: 2em;
  }

  .box > .button-box {
    padding-top: 40px;
  }

  .box > .button-box > button {
    display: block;
    width: 100%;
  }
</style>



