<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
  export default {
    name: 'app'
  }
</script>

<style>
  body {
    font-family: 'Raleway', Helvetica, Tahoma, Arial, "Hiragino Sans GB", "Microsoft YaHei", "WenQuanYi Micro Hei", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 14px;
    /*background-color: #fafafa;*/
    color: #262626;
    margin: auto;
  }

  #app {
  }

  .mint-popup-text {
    top: -10px;
    width: 95%;
    padding: 24px 14px 14px 14px;
    border-radius: 8px;
    box-sizing: border-box;
    text-align: center;
    position: fixed;
    background-color: #fff;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, .1);
    transition: opacity .3s, transform .3s, left .3s, right .3s, top .4s, bottom .3s;
    overflow: hidden;
    color: #606266;
  }

  header {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    height: 45px;
    line-height: 45px;
    box-sizing: border-box;
    text-align: center;
    padding: 0 10px;
    font-size: 18px;
    border-bottom: 1px solid #ebeef5;
    overflow: hidden;
    background-color: #fff;
    z-index: 1;
  }

  header > i {
    font-size: 20px !important;
    display: block;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }

  header > i.left {
    float: left;
  }

  header + * {
    margin-top: 45px !important;
  }

  .stripes {
    height: 2px;
    width: 100%;
    background-size: 40px 40px; /* 控制条纹的大小 */
  }

  .angled-first {
    background-color: #f90;
  }

  .angled-last {
    background-color: #ac0;
  }

  .angled {
    background-image: linear-gradient(45deg, rgba(255, 255, 255, .2) 25%, transparent 25%,
    transparent 50%, rgba(255, 255, 255, .2) 50%, rgba(255, 255, 255, .2) 75%,
    transparent 75%, transparent);
  }
</style>



