import Vue from 'vue';
import Router from 'vue-router';
import Login from '@/components/Login';
import Index from '@/components/Index';
import SeeNum from '@/components/SeeNum';
import Verification from '@/components/Verification'


Vue.use(Router);

export default new Router({
  mode: 'history',
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition;
    } else if (to.hash) {
      return {selector: to.hash}
    } else {
      return {x: 0, y: 0}
    }
  },
  routes: [
    {
      path: '/dbhexiao',
      redirect: '/dbhexiao/Login'
    },
    {
      path: '/dbhexiao/Login',
      name: 'Login',
      component: Login
    },
    {
      path: '/dbhexiao/Index',
      name: 'Index',
      component: Index
    },
    {
      path: '/dbhexiao/SeeNum',
      name: 'SeeNum',
      component: SeeNum
    },
    {
      path: '/dbhexiao/Verification',
      name: 'Verification',
      component: Verification
    }
  ]
})



