<template>
  <div class="see-num">
    <header><i class="mintui mintui-back left" @click="$router.push({name:'Index'})"></i>查看数量</header>
    <mt-loadmore
      :topMethod="loadTop"
      :bottomMethod="loadBottom"
      :bottomPullText="bottomPullText"
      :bottomDropText="bottomDropText"
      :bottomAllLoaded="false"
      ref="loadmore">
      <ul>
        <li class="cell-box" v-for="(item, index) in TableData">
          <div class="angled stripes" :class="(index+1)%2===0?'angled-first':'angled-last'"></div>
          <mt-cell title="序号" :value="item.id"></mt-cell>
          <mt-cell title="商品名称" :value="item.name"></mt-cell>
          <mt-cell title="总数" :value="item.totalnum"></mt-cell>
          <mt-cell title="未核销" :value="item.hexiaonum"></mt-cell>
          <div class="angled stripes"
               v-if="index===(TableData.length-1)"
               :class="((index+1)%2===0)?'angled-last':'angled-first'"></div>
        </li>
      </ul>
    </mt-loadmore>

    <mt-popup v-model="popupVisible" position="top" class="mint-popup-text" :modal="false">
      {{popupText}}
    </mt-popup>
  </div>
</template>

<script>
  import {Loadmore, Cell, Button, Indicator, Popup} from 'mint-ui';
  import qs from 'qs';

  export default {
    name: "see-num",
    data() {
      return {
        TableData: [],
        popupVisible: false,
        popupText: '',
        nowpage: 1,
        allLoaded: false,
        bottomPullText: '上拉加载更多',
        bottomDropText: '释放加载更多',
      }
    },
    created() {
      this.getTableData({
        loading: () => {
          Indicator.open({
            text: '请求数据中...',
            spinnerType: 'fading-circle'
          });
        },
        loadingClose: () => {
          Indicator.close();
        },
        isAgen: true
      });
    },
    methods: {
      getTableData(opt) {
        let self = this;
        opt.loading && opt.loading();
        self.$http.post(self.baseUrl + '/dbhexiao/admin.php/Admin/Product/productnum', qs.stringify({p: self.nowpage})).then((result) => {
          opt.loadingClose && opt.loadingClose();
          if (parseInt(result.data.code) === 1 && typeof result.data.data === "object") {
            let temp = result.data.data;
            let temp_arr = [];
            temp.list.forEach((v) => {
              if (opt.isAgen) temp_arr.push(v); else self.TableData.push(v);
            });
            if (opt.isAgen) {
              self.popupText = result.data.message;
              self.popupVisible = true;
              setTimeout(() => {
                self.popupVisible = false;
              }, 2000);
              self.TableData = temp_arr;
            }
            if (parseInt(temp.totalpage) === parseInt(temp.nowpage)) {
              self.bottomPullText = '已经没有更多信息了';
              self.bottomDropText = '已经没有更多信息了';
              self.allLoaded = true;// 若数据已全部获取完毕
            } else {
              self.bottomPullText = '上拉加载更多';
              self.bottomDropText = '释放加载更多';
              self.allLoaded = false;
            }
            self.nowpage = parseInt(temp.nowpage) + 1;
          } else {
            self.popupText = result.data.message;
            self.popupVisible = true;
            setTimeout(() => {
              self.popupVisible = false;
            }, 2000);
          }
        }, (error) => {
          self.popupText = '网络连接失败！';
          self.popupVisible = true;
          setTimeout(() => {
            self.popupVisible = false;
          }, 2000);
          opt.loadingClose && opt.loadingClose();
        })
      },
      loadTop() {
        let self = this;
        self.nowpage = 1;
        self.allLoaded = false;
        self.getTableData({
          loadingClose: () => {
            self.$refs.loadmore.onTopLoaded();
          },
          isAgen: true
        });
      },
      loadBottom() {
        let self = this;
        if (self.allLoaded) {
          self.$refs.loadmore.onBottomLoaded();
        }
        else {
          self.getTableData({
            loadingClose: () => {
              self.$refs.loadmore.onBottomLoaded();
            },
          });
        }
      }
    },
    components: {
      'mt-loadmore': Loadmore,
      'mt-cell': Cell,
      'mt-button': Button,
      'mt-popup': Popup,
    },
  }
</script>

<style scoped>
  .see-num > .mint-loadmore > .mint-loadmore-content > ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
</style>



