<template>
  <div class="verification">
    <header>
      <i class="mintui mintui-back left" @click="page===0?$router.push({name:'Index'}):page=0"></i>
      {{title}}
    </header>
    <div class="select-box" v-if="page===0">
      <div class="input-box">
        <mt-field label="兑换码" placeholder="请输入兑换码" v-model="keystr"></mt-field>
      </div>
      <div class="btn-box">
        <mt-button type="primary" @click="selectData">确认</mt-button>
        <mt-button type="danger" @click="keystr=''">清空</mt-button>
      </div>
    </div>
    <div class="list-box" v-if="page===1">
      <div class="angled stripes angled-first"></div>
      <template v-if="isnull">
        <p style="height:100px;line-height:100px;font-size:18px;text-align:center;">暂无相关数据</p>
      </template>
      <template v-else>
        <mt-cell title="商品名称" :value="listData.name"></mt-cell>
        <mt-cell title="数量" :value="listData.productnum"></mt-cell>
        <mt-cell title="兑换码" :value="listData.keystr"></mt-cell>
        <mt-cell title="状态" :value="listData.status_str"></mt-cell>
      </template>

      <div class="angled stripes angled-last"></div>
      <div class="btn-box" v-if="!isnull">
        <mt-button type="primary" @click="verification" v-if="listData.status==='3'">一键核销</mt-button>
        <mt-button type="danger" @click="" v-else>已核销</mt-button>
      </div>
      <div v-else>
        <mt-button type="primary" @click="page=0" style="width:90%;display:block;margin:30px auto;">返回</mt-button>
      </div>
    </div>

    <mt-popup v-model="popupVisible" position="top" class="mint-popup-text" :modal="false">
      {{popupText}}
    </mt-popup>
  </div>
</template>

<script>
  import {Field, Cell, Button, Indicator, Popup, MessageBox} from 'mint-ui';
  import qs from 'qs';

  export default {
    name: "verification",
    data() {
      return {
        title: '核销',
        popupVisible: false,
        popupText: '',
        keystr: '',
        listData: {
          id: '',
          keystr: '',
          name: '',
          productnum: '',
          status: '',
          status_str: ''
        },
        isnull: false,
        page: 0,
      }
    },
    methods: {
      selectData(flag = true) {
        let self = this;
        if (self.keystr.trim() === '') {
          self.popupText = '兑换码不能为空！';
          self.popupVisible = true;
          setTimeout(() => {
            self.popupVisible = false;
          }, 2000);
          return;
        }
        Indicator.open({
          text: '请求数据中...',
          spinnerType: 'fading-circle'
        });
        self.$http.post(self.baseUrl + '/dbhexiao/admin.php/Admin/Hexiao/index', qs.stringify({keystr: self.keystr})).then((result) => {
          Indicator.close();
          if (parseInt(result.data.code) === 1 && typeof result.data.data === "object") {
            self.page = 1;
            if (!(result.data.data.length > 0))
              self.isnull = true;
            else {
              self.isnull = false;
              let temp = result.data.data[0];
              self.listData.id = temp.id;
              self.listData.keystr = temp.keystr;
              self.listData.name = temp.name;
              self.listData.productnum = temp.productnum;
              self.listData.status = temp.status;
              self.listData.status_str = temp.status === '3' ? '未核销' : '已核销';
            }
          }
          if (flag) {
            self.popupText = result.data.message;
            self.popupVisible = true;
            setTimeout(() => {
              self.popupVisible = false;
            }, 2000);
          }

        }, (error) => {
          Indicator.close();
          self.popupText = '网络连接失败！';
          self.popupVisible = true;
          setTimeout(() => {
            self.popupVisible = false;
          }, 2000);
        });
      },
      verification() {
        let self = this;
        MessageBox({
          title: '提示',
          message: '确定执行此操作?',
          showCancelButton: true
        }).then(action => {
          if (action !== 'cancel') {
            Indicator.open({
              text: '核销中...',
              spinnerType: 'fading-circle'
            });
            self.$http.post(self.baseUrl + '/dbhexiao/admin.php/Admin/Hexiao/hexiao', qs.stringify({
              id: self.listData.id,
              keystr: self.listData.keystr
            })).then((result) => {
              Indicator.close();
              if (parseInt(result.data.code) === 1 && typeof result.data.data === "object") {
                let temp = result.data.data;
                self.selectData(false);
              }
              self.popupText = result.data.message;
              self.popupVisible = true;
              setTimeout(() => {
                self.popupVisible = false;
              }, 2000);
            }, (error) => {
              Indicator.close();
              self.popupText = '网络连接失败！';
              self.popupVisible = true;
              setTimeout(() => {
                self.popupVisible = false;
              }, 2000);
            });
          }
        });
      }
    },
    components: {
      'mt-field': Field,
      'mt-cell': Cell,
      'mt-button': Button,
      'mt-popup': Popup,
    },
  }
</script>

<style scoped>
  .verification > .select-box > .btn-box {
    width: 90%;
    margin: 30px auto;
  }

  .verification > .select-box > .btn-box > button {
    width: calc(50% - 2.5px);
  }

  .list-box > .btn-box {
    padding: 30px 0;
  }

  .list-box > .btn-box > button {
    display: block;
    width: 90%;
    margin: 0 auto;
  }
</style>



