<template>
  <div class="index">
    <header>信游星空</header>
    <div class="btn-box">
      <mt-button type="default" class="btn-radiu" style="border-color: #67c23a;color: #67c23a;" plain @click="$router.push({name:'Verification'})">核销</mt-button>
      <mt-button type="primary" class="btn-radiu" plain @click="$router.push({name:'SeeNum'})">查看数量</mt-button>
      <mt-button type="danger" class="btn-radiu" plain @click="logout_func">退出</mt-button>
    </div>
    <mt-popup v-model="popupVisible" position="top" class="mint-popup-text" :modal="false">
      {{popupText}}
    </mt-popup>
  </div>
</template>

<script>
  import {Button, Indicator, Popup} from 'mint-ui';

  export default {
    name: "index",
    data() {
      return {
        popupVisible: false,
        popupText: '',
      }
    },
    methods: {
      logout_func() {
        let self = this;
        Indicator.open({
          text: '退出中...',
          spinnerType: 'fading-circle'
        });
        self.$http.post(self.baseUrl + '/dbhexiao/admin.php/Admin/Login/logout').then((result) => {
          Indicator.close();
          if (parseInt(result.data.code) === 1 && typeof result.data.data === "object") {
            let temp = result.data.data;
            self.$router.push({name: 'Login'});
          } else {
            self.popupText = result.data.message;
            self.popupVisible = true;
            setTimeout(() => {
              self.popupVisible = false;
            }, 2000);
          }
        }, (error) => {
          self.popupText = '网络连接失败！';
          self.popupVisible = true;
          setTimeout(() => {
            self.popupVisible = false;
          }, 2000);
          Indicator.close();
        })
      }
    },
    components: {
      'mt-button': Button,
      'mt-popup': Popup,
    },
  }
</script>

<style scoped>
  .index > .btn-box {
    position: absolute;
    top: 50%;
    width: 100%;
    transform: translateY(-50%);
  }

  .index > .btn-box > .btn-radiu {
    display: block;
    width: 70%;
    border-radius: 25px;
    height: 50px;
    line-height: 50px;
    margin: 40px auto;
    font-size: 16px;
  }
</style>



